@import "../../../App/App";

.checkbox {
  opacity: 0;
  position: absolute;
}

.DarkModeButton {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 20px;
}

.label {
  width: 50px;
  height: 26px;
  background-color: $--blue;
  display: flex;
  border-radius: 50px;
  align-items: center;
  justify-content: space-between;
  padding: 5px;
  position: relative;
  transform: scale(1.5);
  cursor: pointer;
}

.ball {
  width: 20px;
  height: 20px;
  background-color: white;
  position: absolute;
  top: 2px;
  left: 2px;
  border-radius: 50%;
  transition: transform 0.2s linear;
}

/*  target the elemenent after the label*/
.checkbox:checked + .label .ball {
  transform: translateX(24px);
}

.fa-moon {
  color: pink;
  width: 15px;
}

.fa-sun {
  width: 15px;
  color: yellow;
}
